	import top from '@/components/common/top.vue'
	import tabBar from '@/components/common/tab-bar.vue'
	import empty from '@/components/common/empty.vue'
	import uniNumberBox from "@/components/uni-number-box/uni-number-box.vue"
	import { mapState, mapMutations } from 'vuex'
	import product from '@/static/data/product.js'
	import like from '@/static/data/like.js'
	export default {
		data() {
			return {
				totalPrice: 0,
				totalNum: 0,
				check_goods: [],
				shopCartList: [],
				likeList: like,
			}
		},
		computed: {
			...mapState([
				'cartList'
			]),
			checkeds() {
				let checkeds = []
				this.cartList.forEach((value,inde)=>{
					checkeds.push(false);
				})
				return checkeds;
			},
			isEmpty() {
				if(this.shopCartList != 0){
					return false;
				}else{
					return true;
				}
			}
		},
		methods: {
			...mapMutations([
				'delCartItem',
				'addCartItemNum',
				'reduceCartItemNum'
			]),
			change(index){
				this.checkeds[index] = !this.checkeds[index];
				this.calTotalPrice()
				this.calTotalNum()
			},
			calTotalPrice() {
				this.totalPrice = 0;
				this.shopCartList.forEach((value,index)=>{
					if(this.checkeds[index]){
						this.totalPrice += value.price * value.num;
					}
				})
			},
			calTotalNum() {
				this.totalNum = 0;
				this.cartList.forEach((value,index)=>{
					if(this.checkeds[index]){
						this.totalNum += value.num;
					}
				})
			},
			changeNum(way,index) {
				if(way>0){
					this.addCartItemNum(index)
				}else{
					this.reduceCartItemNum(index)
				}
				this.getShopCartList();
				this.calTotalPrice();
				this.calTotalNum();
				
			},
			delProduct(index){
				this.shopCartList.splice(index,1)
				this.delCartItem(index)
				this.calTotalPrice()
				this.calTotalNum()
			},
			toHome() {
				uni.navigateTo({
					url: '../../pages/index/index'
				})
			},
			toCheckout() {
				uni.navigateTo({
					url: '../../pages/order/checkout/index'
				})
			},
			getShopCartList() {
				this.shopCartList = [];
				let cartList = this.cartList;
				cartList.forEach((item,i)=>{
					product.forEach((value,index)=>{
						if(value.typeId == item.typeId){
							let arr = value.goodsList;
							arr.forEach((val,ind)=>{
								if(val.goodsId == item.goodsId){
									val.num = item.num;
									this.shopCartList.push(val);
								}
							})
						}
					})
				})
				console.log(this.shopCartList);
				if(this.shopCartList != 0){
					this.isEmpty = false;
				}
			}
		},
		components: {
			top,
			tabBar,
			uniNumberBox,
			empty,
		},
		onLoad(option) {
			console.log(this.isEmpty);
			this.calTotalPrice()
			this.calTotalNum()
			this.getShopCartList()
			// console.log(this.isEmpty);
// 			if(this.cartList == []){
// 				console.log('1');
// 				this.isEmpty = true;
// 			}else{
// 				console.log('2');
// 				this.isEmpty = true;
// 			}
		},
	}
